
.code-group-item {
  @apply hidden border-t border-dashed border-reco-border-lightmode;
  @apply dark:border-reco-border-darkmode;
  margin-top: -1px;
  &.code-group-item__active {
    @apply block;
  }
  div[class*="language-"] {
    @apply mt-0 mb-0 border-none;
  }
  .line-numbers {
    @apply pb-4 !important;
  }
}
