
.badge {
  display: inline-block;
  font-size: 14px;
  height: 18px;
  line-height: 18px;
  border-radius: 3px;
  padding: 0 6px;
  @apply text-white;

  &.tip {
    @apply bg-reco-primary;
  }

  &.warning {
    @apply bg-reco-container-warning;
  }

  &.danger {
    @apply bg-reco-container-danger;
  }

  & + & {
    margin-left: 5px;
  }
}
