
.xicon-container {
  @apply inline-flex cursor-pointer;
  &.left {
    @apply flex-row items-center;
    > svg {
      @apply mr-1.5;
    }
  }
  &.right {
    @apply flex-row-reverse items-center;
    > svg {
      @apply ml-1.5;
    }
  }
  &.top {
    @apply flex-col items-center;
    > svg {
      @apply mb-1.5;
    }
  }
  &.bottom {
    @apply flex-col-reverse items-center;
    > svg {
      @apply mt-1.5;
    }
  }
}
